<template><!------------充值地址----------------->
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入会员昵称、UID、手机号码"
                      @keyup.native.enter="handleFilter"
                      v-model="listQuery.keyword">
            </el-input>
            <el-button class="filter-item" @click="handleFilter" size="small" style="margin-left: 30px">查询</el-button>

        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间"
                    style="width: 100%; ">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
            </el-table>
            <div v-show="!listLoading" class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="listQuery.page"
                               :page-sizes="[15,30,45,60]" :page-size="listQuery.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
        <!------------------------------------------------下面为查看身份信息---------------------------------------------------------------------------->
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import {getgoodslist} from '@/api/useraddress';

    export default {
        data() {
            return {
                //总条数
                total: null,
                //返回来列表的数据
                tableData: [],
                listLoading: true,
                listQuery: {
                    keyword: '',
                    page: 1,
                    pageSize: 15
                },
                tableHeader: [
                    {
                        label: "UID",
                        prop: "uid",
                        width: 108
                    }, {
                        label: "登录名",
                        prop: "signtype",
                        width: 180,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? row.phoneNum : (value === 2 ? row.email : '未知');
                        }
                    }, {
                        label: "邮箱地址",
                        prop: "email",
                        width: 170
                    }, {
                        label: "真实姓名",
                        prop: "name",
                        width: 180
                    }, {
                        label: "收货手机号",
                        prop: "goodsphone",
                        width: 180
                    }, {
                        label: "所在区域",
                        prop: "coinName",
                        width: 230,
                    }, {
                        label: "详细地址",
                        prop: "address",
                        width: 420,

                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        width: 200,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }

                    }
                ]
            }

        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {
            getList() {
                this.listLoading = true;
                getgoodslist(this.listQuery).then(response => {
                    this.tableData = response.data.list;
                    this.total = response.data.total;
                    this.listLoading = false;
                })
            },
            /**
             * 条件筛选查询对象
             */
            handleFilter() {
                this.getList();
            },
            //获取当前页数
            userPage(val) {
                this.listQuery.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.listQuery.pageSize = val;
                this.getList();
            },

        },
        created() {
            this.getList();
        }
    }
</script>


